ea múltiples debates sobre el equilibrio entre las partes, la función social de la propiedad, la protección de la vivienda como derecho fundamental, y los límites a la autonomía de la voluntad. Por ejemplo, la Corte Constitucional ha señalado en distintas sentencias, la importancia de garantizar el derecho a una vivienda digna frente a medidas de desalojo, especialmente cuando están en juego derechos de personas vulnerables. También ha reconocido la validez de pactos especiales sobre incrementos, cláusulas penales o prohibiciones de subarriendo, siempre que no vulneren el marco legal ni los derechos mínimos del arrendatario. ")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5 justify-content-center"},[_c('div',{staticClass:"col-6 col-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/16.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-10"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("En este sentido, el contrato de arrendamiento "),_c('b',[_vm._v("no es una figura cerrada ni mecánica")]),_vm._v(", sino un instrumento jurídico vivo, que debe interpretarse a la luz del contexto social, económico y normativo en que se aplica.")]),_c('p',[_vm._v("Para ilustrar su aplicación práctica, imagine que usted como abogado representa a una persona que arrienda un apartamento a una familia, por un término de un año, con un canon mensual de $1.500.000. El contrato fue firmado por escrito e incluyó una cláusula de incremento anual conforme al IPC. A los seis meses, el arrendatario deja de pagar. En este escenario, su obligación será primero verificar si se cumplieron todos los requisitos legales, enviar una notificación formal por mora, intentar una conciliación extrajudicial si corresponde y, en caso de persistir el incumplimiento, iniciar el proceso judicial de restitución. Ahora bien, si el arrendador no cumplió con las reparaciones prometidas y el apartamento presenta condiciones que afectan su habitabilidad, podría ser el arrendatario quien solicite la terminación del contrato por justa causa. Ambos casos demuestran cómo el conocimiento preciso del arrendamiento, permite actuar con criterio y defender los derechos de sus clientes.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width bg-color-1"},[_c('div',{staticClass:"px-4 px-md-5"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-7 mb-3 mb-lg-0",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("En conclusión, "),_c('b',[_vm._v("el contrato de arrendamiento")]),_vm._v(" es mucho más que un acuerdo para el uso de un bien: es una construcción jurídica compleja que regula el equilibrio entre el acceso a bienes ajenos y la protección de la propiedad privada. Dominar sus elementos esenciales, su régimen normativo, sus características y consecuencias prácticas, es indispensable para el ejercicio profesional del Derecho, tanto en la asesoría como en la prevención y resolución de conflictos. Un abogado o abogada con sólidos conocimientos en esta materia, no solo redacta contratos claros y eficaces, sino que también identifica riesgos, propone soluciones, y representa con seguridad los intereses de sus clientes, ante tribunales o autoridades administrativas. El arrendamiento, en suma, es una puerta de entrada al ejercicio jurídico con impacto real en la vida de las personas y en el desarrollo económico del país.")])]),_c('div',{staticClass:"col-lg-5",attrs:{"data-aos":"zoom-in"}},[_c('img',{staticClass:"img-fluid mx-auto mx-lg-0 ms-lg-auto mb-4 mb-lg-0",staticStyle:{"max-width":"486px"},attrs:{"src":require("@/assets/curso/tema3/17.svg"),"alt":""}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-12 col-md-6 col-lg-3 offset-lg-1"},[_c('figure',[_c(